@use "../base/colors" as *;

.navbar {
	display: flex;
	align-items: center;
	position: fixed;
	top: 0;
	padding: 1em 0;
	z-index: 123123;
	background-color: rgba(0, 0, 0, 0.1);
	width: 100%;
	&__links {
		display: flex;
		flex-direction: row;
		align-items: center;
		height: 80px;
		gap: 6em;
	}
	&__link {
		font-size: 2.4rem;
		color: #fff;
		font-weight: 300;
		transition: color 0.3s;
		&:hover {
			color: $main-color;
		}
	}
}
.line {
	margin: 0.3em;
	height: 0.2em;
	background-color: #fff;
	border-radius: 20px;
	transition: background-color 0.3s, width 0.3s;
	&:nth-child(1) {
		width: 1.4em;
	}
	&:nth-child(2) {
		width: 3em;
	}
	&:nth-child(3) {
		width: 1.2em;
		margin-left: auto;
	}
}
.burger {
	display: flex;
	flex-direction: column;
	background: transparent;
	border: none;
	outline: none;
	cursor: pointer;
	&:focus {
		.line {
			width: 3em;
			background-color: $main-color;
		}
	}
}
