@use "../abstracts/mixins" as *;
@use "../base/colors" as *;

.header {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	padding-top: 20em;
	background-color: #1d1d1d;
	background-image: url("../../../dist/img/bgc.svg");
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	overflow: hidden;
	&__content {
		display: flex;
	}
	// &__hero {
	// 	position: absolute;
	// 	left: 0;
	// 	bottom: 0;
	// 	height: 1000px;
	// }
	&__hero {
		width: 50%;
		margin-left: -25rem;
		margin-bottom: -2rem;
		object-fit: contain;
	}
	&__right {
		position: relative;
		display: flex;
		align-items: flex-end;
		// & img {
		// 	height: 50%;
		// }
		// &::after {
		// 	position: absolute;
		// 	content: "";
		// 	bottom: 0;
		// 	left: 0;
		// 	background-repeat: no-repeat;
		// 	background-size: contain;
		// 	height: 100%;
		// 	width: auto;
		// 	background-image: url("../../../dist/img/postac1.svg");
		// }
	}
	&__left,
	&__right {
		width: 50%;
		height: 100%;
	}
	&__left {
		background-color: #1d1d1d;
		color: #fff;
		display: flex;
		flex-direction: column;
		align-self: center;
		margin-right: 25em;
		gap: 1em;
		&-text {
			font-size: clamp(2.8rem, 2rem + 2.2222vw, 4rem);
			font-weight: 600;
		}
		& span {
			font-size: clamp(1.2rem, 0.4rem + 2.2222vw, 2.4rem);
			font-weight: 300;
		}
		&-description {
			font-size: clamp(1.2rem, 0.4rem + 2.2222vw, 2.4rem);
			font-weight: 300px;
		}
		&-heading {
			font-size: clamp(2rem, 1.2rem + 2.2222vw, 3.2rem);
			font-weight: 500;
			color: $main-color;
		}
		&-button {
			padding: 0.6em 2em;
			font-size: 2rem;
			margin-top: 2em;
			color: $main-color;
			border: 2px solid $main-color;
			width: max-content;
			border-radius: 6px;
			transition: background-color 0.3s;
			&:hover {
				background-color: #f2ac202f;
			}
		}
	}
}
