* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	font-family: "Poppins", sans-serif;
}
body {
	width: 100%;
	height: 100%;
	overflow-x: hidden !important;
}

body,
html {
	scroll-behavior: smooth;
	scroll-padding-top: 80px;
	font-size: 62.5%;
}

.container {
	margin: 0 auto;
	width: 100%;
	max-width: 1400px;
	padding: 0em 4em;
}

a {
	text-decoration: none;
	color: #000;
}
ul {
	list-style-type: none;
}
