@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/success" as *;
@use "./pages/process" as *;
@use "./pages/faq" as *;
@use "./pages/quote" as *;
@use "./pages/contact" as *;
@use "./layout/footer" as *;

.section {
    &-padding{
        padding: 15em 0;
    }
	&__box {
		display: flex;
        flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-weight: 600;
		&-heading {
			font-size: 4.8rem;
			font-weight: 700;
		}
		&-text {
			font-size: 3.6rem;
			font-weight: 600;
		}
		&-span {
			color: $main-color;
		}
	}
}
