* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden !important;
}

body,
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  font-size: 62.5%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
  padding: 0em 4em;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  list-style-type: none;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 20em;
  background-color: #1d1d1d;
  background-image: url("../../../dist/img/bgc.svg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}
.header__content {
  display: flex;
}
.header__hero {
  width: 50%;
  margin-left: -25rem;
  margin-bottom: -2rem;
  object-fit: contain;
}
.header__right {
  position: relative;
  display: flex;
  align-items: flex-end;
}
.header__left, .header__right {
  width: 50%;
  height: 100%;
}
.header__left {
  background-color: #1d1d1d;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-right: 25em;
  gap: 1em;
}
.header__left-text {
  font-size: clamp(2.8rem, 2rem + 2.2222vw, 4rem);
  font-weight: 600;
}
.header__left span {
  font-size: clamp(1.2rem, 0.4rem + 2.2222vw, 2.4rem);
  font-weight: 300;
}
.header__left-description {
  font-size: clamp(1.2rem, 0.4rem + 2.2222vw, 2.4rem);
  font-weight: 300px;
}
.header__left-heading {
  font-size: clamp(2rem, 1.2rem + 2.2222vw, 3.2rem);
  font-weight: 500;
  color: #f2ab20;
}
.header__left-button {
  padding: 0.6em 2em;
  font-size: 2rem;
  margin-top: 2em;
  color: #f2ab20;
  border: 2px solid #f2ab20;
  width: max-content;
  border-radius: 6px;
  transition: background-color 0.3s;
}
.header__left-button:hover {
  background-color: rgba(242, 172, 32, 0.1843137255);
}

.navbar {
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  padding: 1em 0;
  z-index: 123123;
  background-color: rgba(0, 0, 0, 0.1);
  width: 100%;
}
.navbar__links {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 80px;
  gap: 6em;
}
.navbar__link {
  font-size: 2.4rem;
  color: #fff;
  font-weight: 300;
  transition: color 0.3s;
}
.navbar__link:hover {
  color: #f2ab20;
}

.line {
  margin: 0.3em;
  height: 0.2em;
  background-color: #fff;
  border-radius: 20px;
  transition: background-color 0.3s, width 0.3s;
}
.line:nth-child(1) {
  width: 1.4em;
}
.line:nth-child(2) {
  width: 3em;
}
.line:nth-child(3) {
  width: 1.2em;
  margin-left: auto;
}

.burger {
  display: flex;
  flex-direction: column;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.burger:focus .line {
  width: 3em;
  background-color: #f2ab20;
}

.success {
  background-color: #EFF7FC;
}
.success__numbers {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8em;
}
.success__box {
  display: flex;
  gap: 4em;
  justify-content: center;
  align-items: center;
  padding: 2em 0;
}
.success__number {
  font-size: 20rem;
  color: #EFF7FC;
  text-shadow: 2px 2px 0 #FFA216, -2px -2px 0 #FFA216, 2px -2px 0 #FFA216, -2px 2px 0 #FFA216;
}
.success__text {
  padding: 10em 0;
}
.success__text-heading {
  font-size: 2.8rem;
}
.success__text-description {
  font-size: 1.6rem;
}

.section-padding {
  padding: 15em 0;
}
.section__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: 600;
}
.section__box-heading {
  font-size: 4.8rem;
  font-weight: 700;
}
.section__box-text {
  font-size: 3.6rem;
  font-weight: 600;
}
.section__box-span {
  color: #f2ab20;
}