@use "./abstracts/mixins" as *;
@use "../base/colors" as *;

.success {
    background-color: #EFF7FC;
	&__numbers {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 8em;
	}

	&__box {
        display: flex;
        gap: 4em;
        justify-content: center;
        align-items: center;
		padding: 2em 0;
	}
    
    &__number{
        font-size: 20rem;
        color: #EFF7FC;
        text-shadow: 2px 2px 0 #FFA216, -2px -2px 0 #FFA216, 2px -2px 0 #FFA216, -2px 2px 0 #FFA216;
    }
    &__text{
        padding: 10em 0;
        &-heading{
            font-size: 2.8rem;
        }
        &-description{
            font-size: 1.6rem;
        }
    }
}
